import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';

import { AppComponent } from './app.component';
import { AppPageNotFoundComponent } from './app-page-not-found/app-page-not-found.component';
import { ChartOneAppComponent } from './chart-one-app/chart-one-app.component';
import { ChartTwoAppComponent } from './chart-two-app/chart-two-app.component';

const appRoutes: Routes = [
  { path : 'HighChartDemo/chart/one', component: ChartOneAppComponent },
  { path : 'HighChartDemo/chart/two', component: ChartTwoAppComponent },
  { path : 'HighChartDemo', redirectTo: 'HighChartDemo/chart/one', pathMatch: 'full' },
  { path : '**', component: AppPageNotFoundComponent }
];


@NgModule({
  declarations: [
    AppComponent,
    AppPageNotFoundComponent,
    ChartOneAppComponent,
    ChartTwoAppComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    RouterModule.forRoot(appRoutes, {enableTracing : false})
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



