import { Component, OnInit } from '@angular/core';
import { HttpProviderAppService, ISeries } from '../data-provider/http-provider-app.service';
declare var $: any;

@Component({
  selector: 'app-chart-two-app',
  templateUrl: './chart-two-app.component.html',
  styleUrls: ['./chart-two-app.component.css'],
  providers: [HttpProviderAppService]
})
export class ChartTwoAppComponent implements OnInit {

  chart = { type: 'bar' };
  title = {text: 'Monthly Average Temperature'};
  subtitle = { text: 'Source: WorldClimate.com'};
  yAxis = {
    min: 0,
    title: { text: 'Temperature (\xB0C)', align: 'high' },
    labels: { overflow: 'justify' }
  };
  tooltip = { valueSuffix: ' millions' };
  legend = {
    layout: 'vertical',
    align: 'right',
    verticalAlign: 'top',
    floating: true,
    x: -40,
    y: 50,
    borderWidth: 1
  };
  categories: String[];
  series: ISeries[];
  json: Ijson;
  drilldownFlag: boolean;
  drillDownBtn: string;
  constructor(private __http: HttpProviderAppService) {
  }
  ngOnInit() {
    this.drilldownFlag = true;
    this.drilldown();
  }

  setcategories(data) {
    this.categories = data;
    console.log(data);
  }

  setseries(data) {
    this.series = data;
    console.log(data);
  }

  drilldown() {
    if (this.drilldownFlag) {
      this.drilldownFlag = false;
      this.drillDownBtn = 'Drill Up';
      this.__http.getXAxisYearlyCategories().subscribe(data => {
        this.setcategories(data);
      });
      this.__http.getYAxisYearlySeries().subscribe(data => {
          this.setseries(data);
      });
    } else {
      this.drilldownFlag = true;
      this.drillDownBtn = 'Drill Down';
      this.__http.getXAxisMonthlyCategories().subscribe(data => {
        this.setcategories(data);
      });
      this.__http.getYAxisMonthlySeries().subscribe(data => {
        this.setseries(data);
      });
    }
    this.makeGraphReady();
    $('#container').highcharts(this.json);
  }

  makeGraphReady() {
    this.json = {
      chart: this.chart,
      title: this.title,
      subtitle: this.subtitle,
      xAxis: {
        categories: this.categories
      },
      yAxis: this.yAxis,
      tooltip: this.tooltip,
      legend: this.legend,
      series: this.series,
      plotOptions: {
        bar: {dataLabels: { enabled: true}},
        series: { stacking: 'normal' }
      },
      credits: { enabled: false }
    };
  }

}

export interface Ijson {
  chart: any;
  title: any;
  subtitle: any;
  xAxis: any;
  yAxis: any;
  tooltip: any;
  legend: any;
  series: any;
  plotOptions: any;
  credits: any;
}




