import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd, NavigationStart } from '@angular/router';
import { HttpProviderAppService, ISeries } from '../data-provider/http-provider-app.service';
import 'rxjs/add/operator/filter';

declare var $: any;

@Component({
  selector: 'app-chart-one-app',
  templateUrl: './chart-one-app.component.html',
  styleUrls: ['./chart-one-app.component.css'],
  providers: [HttpProviderAppService]
})
export class ChartOneAppComponent implements OnInit {

  title = {text: 'Monthly Average Temperature'};
  subtitle = { text: 'Source: WorldClimate.com'};
  yAxis = {
    title: { text: 'Temperature (\xB0C)'},
    plotLines: [{ value: 0, width: 1, color: '#808080' }]
  };
  tooltip = { valueSuffix: '\xB0C' };
  legend = {
    layout: 'vertical',
    align: 'right',
    verticalAlign: 'middle',
    borderWidth: 0
  };
  categories: String[];
  series: ISeries[];
  json: Ijson;
  drilldownFlag: boolean;
  drillDownBtn: string;

  constructor(private __http: HttpProviderAppService, private router: Router) {
    router.events
    .filter(event => event instanceof NavigationEnd)
    .subscribe((event) => {
      this.drilldownFlag = true;
      this.drilldown();
  });
  }
  ngOnInit() {
  }

  setcategories(data) {
    this.categories = data;
    console.log(data);
  }

  setseries(data) {
    this.series = data;
    console.log(data);
  }


  drilldown() {
    if (this.drilldownFlag) {
      this.drilldownFlag = false;
      this.drillDownBtn = 'Drill Up';
      this.__http.getXAxisYearlyCategories().subscribe(data => {
//        this.categories = data;
        this.setcategories(data);
      });
      this.__http.getYAxisYearlySeries().subscribe(data => {
//        this.series = data;
        this.setseries(data);
      });
    } else {
      this.drilldownFlag = true;
      this.drillDownBtn = 'Drill Down';
      this.__http.getXAxisMonthlyCategories().subscribe(data => {
//        this.categories = data;
        this.setcategories(data);
      });
      this.__http.getYAxisMonthlySeries().subscribe(data => {
//        this.series = data;
        this.setseries(data);
      });
    }

    this.makeGraphReady();
    $('#container').highcharts(this.json);
  }

  makeGraphReady() {
    this.json = {
      title: this.title,
      subtitle: this.subtitle,
      xAxis: {
        categories: this.categories
      },
      yAxis: this.yAxis,
      tooltip: this.tooltip,
      legend: this.legend,
      series: this.series
    };
  }
}

export interface Ijson {
  title: any;
  subtitle: any;
  xAxis: any;
  yAxis: any;
  tooltip: any;
  legend: any;
  series: any;
}





